import styled from "styled-components";

export const MessageElement = styled.div`
  padding: 10px;
  border-bottom: 1px #dadcdf solid;
`

export const MessageUserAvatar = styled.img`
  width: 30px;
  border-radius: 15px;
`

export const MessageUserName = styled.b`
  width: 30px;
  padding-left: 7px;
`

export const MessageText = styled.p`
  margin: 0;
  padding: 0 0 0 5px;
  word-wrap: break-word;
`